@InitRoute
fun initHomePage(ctx: InitRouteContext) {
    ctx.data.add(PageLayoutData("Home"))
}

@Page
@Composable
fun HomePage() {
    val colorPalette = ColorMode.currentState.value.toSitePalette()
    Column(
        modifier = Modifier
            .fillMaxSize()
            .padding(all = 1.cssRem)
    ) {
        Row(
            modifier = Modifier
                .fillMaxWidth()
                .padding(leftRight = 5.cssRem)
        ) {
            Spacer()
            ColorModeButton2()
        }
        Row(
            modifier = Modifier
                .fillMaxWidth()
                .weight(1f)
                .padding(leftRight = 10.cssRem),
            verticalAlignment = Alignment.CenterVertically
        ) {
            Column(
                modifier = Modifier
                    .weight(0.6f)
            ) {
                SpanText(
                    "Hello ",
                    modifier = Modifier
                        .fontSize(3.cssRem)
                        .fontWeight(FontWeight.Bold)
                )
                Row {
                    SpanText(
                        "I'm ",
                        modifier = Modifier
                            .fontSize(1.6.cssRem)
                            .fontWeight(FontWeight.Normal)
                    )
                    SpanText(
                        "Gkberk Yac",
                        modifier = Modifier
                            .color(colorPalette.brand.primary)
                            .fontSize(1.6.cssRem)
                            .fontWeight(FontWeight.Normal)
                    )
                }
                SpanText(
                    "I have been developing software for over 10 years, and I am passionate about creating innovative solutions that make a difference.",
                    modifier = Modifier
                        .fontSize(1.6.cssRem)
                        .fontWeight(FontWeight.Normal)
                )
            }

            Box(
                modifier = Modifier
                    .displayIfAtLeast(Breakpoint.LG)
                    .weight(0.1f)
            )

            Box(
                modifier = Modifier
                    .displayIfAtLeast(Breakpoint.LG)
                    .weight(0.3f),
                contentAlignment = Alignment.Center
            ) {
                Img(
                    src = "person.svg",
                    attrs = {
                        attr("alt", "Logo")
                        style {
                            maxWidth(50.percent)
                            height(Height.FitContent)
                        }
                    }
                )
            }
        }

    }
}

@Composable
fun ColorModeButton2() {
    var colorMode by ColorMode.currentState
    IconButton(onClick = { colorMode = colorMode.opposite }) {
        if (colorMode.isLight) MoonIcon() else SunIcon()
    }
    Tooltip(ElementTarget.PreviousSibling, "Toggle color mode", placement = PopupPlacement.BottomRight)
}